<template>
    <div class="index-page">
        <el-container style="height: 100%;">
            <el-aside width="25%" class="left-side-barricade" style="padding: 1%;">
                <SideBarricade />
            </el-aside>
            <el-main class="right-side-content" style="height: 90vh; overflow-y: auto;">
                <el-empty v-if="!hotPostsAll.length" description="这里好像什么都没有"></el-empty>
                <div v-else class="main">
                    <el-card class="main-header">留言板</el-card>
                    <span>
                        <el-switch v-model="method" active-text="最新" inactive-text="最热"></el-switch>
                    </span>
                    <transition-group name="post-fade" tag="div" class="contents" v-show="method">
                        <el-card class="post" v-for="(post, index) in hotPostsAll" :key="index">
                            <el-row :gutter="20">
                                <el-col :span="20">
                                    <div class="content">
                                        <router-link :to="`/Post/${post.id}`">
                                            <p>{{ post.title }}</p>
                                        </router-link>
                                        <p>{{ post.summary.slice(30) }}……</p>
                                    </div>
                                    <div class="actions">
                                        <div class="metadata">
                                            <span class="create_time">{{ formatDate(post.create_time) }}</span>
                                            <span class="views_count"> | 浏览 {{ post.views_count }}</span>
                                        </div>
                                    </div>
                                </el-col>
                                <el-col :span="4" class="author-info">
                                    <router-link :to="`/Athor/${post.author.id}`">
                                        <img :src="post.author.image" alt="Author Image" class="author-image" loading="lazy"/>
                                    </router-link>
                                    <span class="name">{{ post.author.username }}</span>
                                </el-col>
                            </el-row>
                        </el-card>
                    </transition-group>
                    <transition-group name="post-fade" tag="div" class="contents" v-show="!method">
                        <el-card class="post" v-for="(post, index) in newPostsAll" :key="index">
                            <el-row :gutter="20">
                                <el-col :span="20">
                                    <div class="content">
                                        <router-link :to="`/Post/${post.id}`">
                                            <p>{{ post.title }}</p>
                                        </router-link>
                                        <p>{{ post.summary.slice(30) }}……</p>
                                    </div>
                                    <div class="actions">
                                        <div class="metadata">
                                            <span class="create_time">{{ formatDate(post.create_time) }}</span>
                                            <span class="views_count"> | 浏览 {{ post.views_count }}</span>
                                        </div>
                                    </div>
                                </el-col>
                                <el-col :span="4" class="author-info">
                                    <router-link :to="`/Athor/${post.author.id}`">
                                        <img :src="post.author.image" alt="Author Image" class="author-image" loading="lazy"/>
                                    </router-link>
                                    <span class="name">{{ post.author.username }}</span>
                                </el-col>
                            </el-row>
                        </el-card>
                    </transition-group>
                </div>
            </el-main>
        </el-container>
    </div>
</template>
<script setup>
import SideBarricade from '@/components/SideBarricade.vue';
import { useUrl } from '@/store/useUrl';
import { get } from '@/http/httpRequest';
import { ref, onMounted,watch} from 'vue';

const { postsList } = useUrl();
const method = ref(true);
const newPostsAll=ref([]);
const hotPostsAll=ref([]);

const formatDate = (str) => {
    const date = new Date(str);
    return `${date.getUTCFullYear()}-${String(date.getUTCMonth() + 1).padStart(2, '0')}-${String(date.getUTCDate()).padStart(2, '0')}`;
};

const init =async ()=>{
    const response=await get(postsList);
    if(response){
        hotPostsAll.value=response.data.slice().sort((a,b)=>{return new Date(a.create_time) < new Date(b.create_time)});
        newPostsAll.value=response.data.slice().sort((a,b)=>{return a.views_count <b.views_count});
    }
}
onMounted(async()=>{
    await init ();
});

</script>
