<template>
    <div class="side-main">
        <canvas id="viewsChart"></canvas>
        <div v-if="predictedViews !== null" class="prediction-message">
            <h4>线性回归预测明日访问量</h4>
            <p>{{ predictedViews.toFixed(0) }} 次</p>
        </div>
        <table class="table table-striped">
            <thead>
                <tr>
                    <th scope="col">No.</th>
                    <th scope="col">日期</th>
                    <th scope="col">访问量</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in paginatedData" :key="index">
                    <th scope="row">{{ index + 1 }}</th>
                    <td>{{ item.time }}</td>
                    <td>{{ item.views }}</td>
                </tr>
            </tbody>
        </table>
        <nav aria-label="Page navigation example">
            <ul class="pagination justify-content-center">
                <li class="page-item" :class="{ disabled: currentPage === 1 }">
                    <a class="page-link" href="#" @click.prevent="changePage(currentPage - 1)">Previous</a>
                </li>
                <li v-for="page in totalPages" :key="page" class="page-item" :class="{ active: page === currentPage }">
                    <a class="page-link" href="#" @click.prevent="changePage(page)">{{ page }}</a>
                </li>
                <li class="page-item" :class="{ disabled: currentPage === totalPages }">
                    <a class="page-link" href="#" @click.prevent="changePage(currentPage + 1)">Next</a>
                </li>
            </ul>
        </nav>
    </div>
    </template>
<script>
import { ref, computed, onMounted } from 'vue';
import { useUrl } from '@/store/useUrl';
import { get } from '@/http/httpRequest';
import { Chart, LineController, LineElement, PointElement, LinearScale, CategoryScale, Title, Tooltip } from 'chart.js';

Chart.register(LineController, LineElement, PointElement, LinearScale, CategoryScale, Title, Tooltip);

export default {
  setup() {
    const data = ref([]);
    const urls = useUrl();
    const currentPage = ref(1);
    const pageSize = ref(8);
    const predictedViews = ref(null); 

    const init = async () => {
      const url = urls.views;
      const response = await get(url);
      if (response) {
        data.value = response.data;
        updateChartData(data.value);
        predictedViews.value = predictTomorrowViews(data.value);
      }
    };

    const updateChartData = (apiData) => {
      const labels = apiData.map(item => item.time);
      const views = apiData.map(item => item.views);
      const ctx = document.getElementById('viewsChart').getContext('2d');
      if (window.myLineChart) {
        window.myLineChart.destroy();
      }
      window.myLineChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [{
            label: '每日访问量',
            data: views,
            borderColor: 'rgba(75, 192, 192, 1)',
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            fill: true,
            tension: 0.4
          }]
        },
        options: {
          responsive: true,
          plugins: {
            title: {
              display: true,
              text: '本站每日访问量统计'
            }
          },
          scales: {
            x: {
              title: {
                display: true,
                text: '日期'
              }
            },
            y: {
              title: {
                display: true,
                text: '访问量'
              },
              beginAtZero: true
            }
          }
        }
      });
    };
    const predictTomorrowViews = (data) => {
      if (!data || data.length < 2) return null;

      let xValues = data.map((_, index) => index + 1); 
      let yValues = data.map(item => item.views);

      function linearRegression(x, y) {
        let n = y.length;
        let sumX = 0, sumY = 0, sumXY = 0, sumXX = 0;

        for (let i = 0; i < n; i++) {
          sumX += x[i];
          sumY += y[i];
          sumXY += x[i] * y[i];
          sumXX += x[i] * x[i];
        }

        let slope = (n * sumXY - sumX * sumY) / (n * sumXX - sumX * sumX);
        let intercept = (sumY - slope * sumX) / n;

        return [slope, intercept];
      }

      let [slope, intercept] = linearRegression(xValues, yValues);
      let nextDayIndex = xValues[xValues.length - 1] + 1;
      let predictedValue = slope * nextDayIndex + intercept;
      return predictedValue;
    };

    const changePage = (page) => {
      if (page >= 1 && page <= totalPages.value) {
        currentPage.value = page;
      }
    };

    const paginatedData = computed(() => {
      const start = (currentPage.value - 1) * pageSize.value;
      const end = start + pageSize.value;
      return data.value.slice(start, end);
    });

    const totalPages = computed(() => {
      return Math.ceil(data.value.length / pageSize.value);
    });

    onMounted(async () => {
      await init();
    });

    return {
      paginatedData,
      currentPage,
      totalPages,
      changePage,
      predictedViews,
    };
  }
};
</script>
